








import Vue from 'vue';
import {Component} from 'vue-property-decorator'

@Component
export default class App extends Vue {
  visible?:boolean = false
  mounted() {
    if(document.documentElement.clientWidth >= 500){
      window.alert('请使用手机打开本页面，以保证浏览效果')
      this.visible = true
    }
  }
  closeOverlay(){
    this.visible = false
  }
}
